.App {
  background-color: rgb(4, 19, 4);
  color: white;
  height: 100vh;
  box-sizing: border-box;
  overflow: auto;
}

input {
  color: rgb(177,253,0);
  font-family: VT323;
  font-size: 20px;
  text-shadow: .5px .5px 2px greenyellow, 0 0 1em greenyellow, 0 0 0.2em greenyellow;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  background-color: transparent;
  width: 100%;
}

.terminal {
  white-space: pre-line;
}

.scan, .bezel {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
}

.scan {
  opacity: 50%;
}

.content {
  color: rgb(177,253,0);
  font-family: VT323;
  font-size: 20px;
  text-shadow: .5px .5px 2px greenyellow, 0 0 1em greenyellow, 0 0 0.2em greenyellow;
  padding: 4rem;
}